.tablist-custom .ant-tabs-nav {
  margin-top: 18px;
  background-color: white;
}

.tablist-custom .ant-tabs-nav::before {
  border-bottom: none !important;
}

.tablist-custom .ant-tabs-nav-wrap {
  padding-left: 16px;
  padding-right: 16px;
}

.tablist-custom .ant-tabs-nav-list {
  padding-top: 4px;
  padding-bottom: 4px;
}

.tablist-custom .ant-tabs-tab:hover {
  color: #00979d !important;
}

.tablist-custom .ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn {
  color: #00979d !important;
  font-weight: 500;
}

.tablist-custom .ant-tabs-ink-bar {
  position: absolute;
  background: #00979d;
  pointer-events: none;
}

.subunitdetail .ant-picker-input > input {
  text-align: center;
  font-weight: 900;
}
