.wrapDevMode {
  box-shadow: 0px 1px 2.5px #d9d9d9;
  border-radius: 10px;
  margin-top: 20px;
  width: fit-content;
  border: 1px solid transparent;
  position: relative;
  background-color: white;
}

.wrapDisconnectedDevMode {
  border: 1px solid red;
  border-radius: 10px;
  margin-top: 20px;
  width: fit-content;
  position: relative;
  background-color: white;
}

.childDevMode {
  margin-top: 10px;
  padding-left: 16px;
  padding-right: 16px;
  padding-bottom: 16px;
}

#wrap {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 1;
}

.wrapButton {
  z-index: 20;
  position: absolute;
  top: 0;
  right: 0;
  opacity: 0;
  transition: opacity .4s ease-in-out;
  -moz-transition: opacity .4s ease-in-out;
  -webkit-transition: opacity .4s ease-in-out;
  -o-transition: opacity .4s ease-in-out;
}

.wrapButton:hover {
  opacity: 1;
}

.buttonSettingDevMode {
  width: 36px;
  margin-left: 2px;
  padding-top: 5px;
  padding-bottom: 5px;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  background-color: white;
  border-width: 1px;
  border: 1px solid #E6E6E6;
}

.wrapDevMode:hover .wrapButton {
  opacity: 1;
}

.wrapDisconnectedDevMode:hover .wrapButton {
  opacity: 1;
}

#widgetInline {
  box-shadow: 0px 1px 2.5px #d9d9d9;
  border-radius: 10px;
  margin-top: 20px;
  width: fit-content;
  border: 1px solid transparent;
  position: relative;
  background-color: white;
  position: relative;
  width: 300px;
  padding-top: 10px;
  padding-left: 20px;
  padding-bottom: 10px;
  padding-right: 20px;
}
