.ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn {
  color: #00979d !important;
}

.ant-tabs-ink-bar {
  background: #00979d;
}

.ant-tabs-top > .ant-tabs-nav::before {
  border-bottom: 0px;
}

.results {
  white-space: nowrap;
  font-size: 16px;
  color: #8a9697;
  margin-right: 30px;
}

.txtShow {
  font-size: 16px;
  color: #8a9697;
  margin-right: 10px;
}

.btnExportExcel {
  margin-left: 10px;
}

.btnExportExcel span {
  color: white;
  font-weight: 600;
}

/* Styled scrollbar */
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: #d1d5d8;
  border-radius: 7px;
}

::-webkit-scrollbar-thumb:hover {
  background: #d1d5d8;
}

.wrapTabHeaderRight {
  position: absolute;
  top: 28px;
  right: 20px;
}

@media screen and (max-width: 768px) {
  .wrapTabHeaderRight {
    top: 74px;
    left: 30px;
    right: unset;
  }

  .ant-tabs-top > .ant-tabs-nav {
    margin-bottom: 32px;
  }
}
