.org-filter-input .ant-input {
  font-weight: 700;
  font-size: 14px;
  color: #fff;
  background-color: #00979d !important;
}

.org-filter-input .ant-input::placeholder {
  font-weight: 700;
  font-size: 14px;
  color: #fff;
}
.ant-select-focused .ant-select-selector,
.select-custom:hover .ant-select-selector {
  border-color: #00979d !important;
  box-shadow: 0 0 0 1px rgba(0, 151, 157, 0.2);
  border-right-width: 1px !important;
}
