.gm-style .gm-style-iw {
  background-color: #fff !important;
  border: 1px solid #00979d;
  top: 0 !important;
  left: 0 !important;
  padding: 0 0 10px !important;
  display: block !important;
  min-width: 250px !important;
}

.gm-style .gm-style-iw .gm-ui-hover-effect {
  display: none !important;
}

.gm-style .gm-style-iw-t::after {
  background-color: #fff !important;
  border-left: 1px solid #00979d;
  border-bottom: 1px solid #00979d;
}

.gm-style .gm-style-iw-d {
  overflow: hidden !important;
}

.iw-header {
  background-color: #00979d;
  padding: 13px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.iw-body {
  padding: 0 13px 16px;
  display: flex;
  flex-wrap: wrap;
}
.gmnoprint {
  padding: 4px;
  background: white;
  border-radius: 8px;
  top: unset !important;
  left: unset !important;
  bottom: 50px;
  right: 52px;
}
.gmnoprint.gm-bundled-control {
  bottom: 50px !important;
}
.gmnoprint.gm-bundled-control .gmnoprint {
  bottom: 0 !important;
  right: -40px !important;
}
.gm-style-mtc button {
  box-shadow: none !important;
}
.gm-style-mtc button:hover {
  background: inherit !important;
}
.gm-style-mtc button[aria-checked='true'] {
  background: #262626 !important;
  color: white !important;
  border-radius: 8px !important;
}
.gmnoprint {
  padding: 0;
}
.ant-radio-checked .ant-radio-inner {
  border-color: #30a7ac !important;
}
.ant-radio-checked .ant-radio-inner::after {
  background-color: #30a7ac !important;
}
.ant-switch-checked {
  background-color: #30a7ac !important;
}
