.org-filter-input .ant-input {
  font-weight: 700;
  font-size: 14px;
  color: #fff;
  background-color: #00979d !important;
}

.org-filter-input .ant-input::placeholder {
  font-weight: 700;
  font-size: 14px;
  color: #fff;
}

.org-item:hover {
  background: rgba(0, 151, 157, 0.1);
}

.ant-select-focused .ant-select-selector {
  box-shadow: none !important;
}

.select-custom-main .ant-select-selector {
  height: 46px !important;
  background: #00979d !important;
  border-radius: unset !important;
  border-color: #00979d !important;
}

.select-custom-main .ant-select-selector .ant-select-selection-item {
  font-weight: 500;
  color: white;
  padding: 8px 0 8px 21px;
}

.select-custom-main .ant-select-arrow {
  margin-right: 30px;
  transform: translateY(-50%);
}

.select-box .ant-select-selector {
  font-weight: 500;
  border-radius: unset !important;
  border-color: white !important;
}

.select-box .ant-select-selector .ant-select-selection-item {
  font-weight: 600;
}

.select-box.ant-select-focused .ant-select-selector {
  border-color: white !important;
  box-shadow: none !important;
}
.dropdown {
  margin-left: -30px;
}
