.iw-header-custom {
  background-color: #CEEFF2;
  padding: 13px;
  padding-bottom: 5px;
  padding-top: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.iw-body {
  padding: 0 13px 16px;
  display: flex;
  flex-wrap: wrap;
  padding: 0;
}

.iw-body .full-row {
  display: flex;
  justify-content: center;
  width: 100%;
}

.point-bullet {
  position: relative;
  margin-left: 3px;
  margin-right: 5px;
}
.point-bullet::before {
  position: absolute;
  content: '';
  width: 3px;
  height: 3px;
  top: 50%;
  transform: translateY(-50%);
  background-color: black;
  clip-path: circle(50% at 50% 50%);
}
.gray-point::before {
  width: 2px;
  height: 2px;
  background-color: #7f9798;
}

.modal-container.ant-modal {
  top: 40%;
  left: 10%;
}

.modal-container .ant-modal-footer {
  border: 0;
  padding: 24px;
  padding-top: 0px;
}

.modal-container .ant-modal-content {
  width: 342px;
  border-radius: 16px;
}

.wrap-text-chip:hover {
  filter: invert(39%) sepia(38%) saturate(4866%) hue-rotate(159deg)
    brightness(100%) contrast(101%);
}

.wrap-text-chip span {
  display: inline-block;
  max-width: 90%;
  white-space: nowrap;
  overflow: hidden !important;
  text-overflow: ellipsis;
}

.text-chip-dashboard-map:hover {
  text-decoration: underline;
}

.iw-custom {
  width: 33%;
}

@media screen and (max-width: 1800px) {
  .wrap-text-chip span {
    max-width: 85%;
  }
}

@media screen and (max-width: 1440px) {
  .iw-custom {
    width: 49.5%;
  }
  .wrap-text-chip span {
    max-width: 90%;
  }
}
