.ant-menu-inline,
.ant-menu-vertical,
.ant-menu-vertical-left {
  border-right: none !important;
}

.treeHook {
  position: absolute;
  content: '';
  width: 20px;
  height: 24px;
  border-left: 1px solid #d4d7dd;
  border-bottom-left-radius: 15px;
  top: 44px;
  left: 30px;
  z-index: 3;
}

@keyframes loading1 {
  0% {
    height: 0;
    border-bottom-left-radius: 0px;
  }
  25% {
    height: 8px;
    border-bottom-left-radius: 0px;
  }
  50% {
    height: 12px;
    border-bottom-left-radius: 0px;
  }
  60% {
    height: 16px;
    border-bottom-left-radius: 0px;
  }
  70% {
    height: 18px;
    border-bottom-left-radius: 0px;
  }
  80% {
    height: 20px;
    border-bottom-left-radius: 6px;
  }
  90% {
    height: 22px;
    border-bottom-left-radius: 8px;
  }
  100% {
    height: 24px;
    border-bottom-left-radius: 15px;
  }
}

@keyframes loading2 {
  0% {
    height: 0;
    border-bottom-left-radius: 0px;
  }
  25% {
    height: 18px;
    border-bottom-left-radius: 0px;
  }
  50% {
    height: 36px;
    border-bottom-left-radius: 0px;
  }
  60% {
    height: 54px;
    border-bottom-left-radius: 0px;
  }
  70% {
    height: 67px;
    border-bottom-left-radius: 5px;
  }
  80% {
    height: 70px;
    border-bottom-left-radius: 10px;
  }
  100% {
    height: 72px;
    border-bottom-left-radius: 15px;
  }
}

.ant-menu > .ant-menu-item {
  color: #8C8C8C;
}

.ant-menu-submenu-title {
  color: #8C8C8C;
  padding-right: 0 !important;
}

.ant-menu > .ant-menu-item-open,
.ant-menu > .ant-menu-item-selected {
  color: white;
  background-color: #00979d !important;
}
