#wrapValueCards {
    box-shadow: 0px 1px 2.5px #D9D9D9 ;
    padding: 10px;
    border-radius: 10px;
    justify-content: space-between;
    display: grid;
    align-items: center;
    width: calc(100vw);
}

.wrapCards::-webkit-scrollbar {
    display: none;
}
.wrapCards {
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}
